import React from 'react';
import './index.css';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import ChatBubbleOutlineIcon from '@material-ui/icons/ChatBubbleOutline';
import SendIcon from '@material-ui/icons/Send';

function Post(props) {
    return(
        <div className="post" >
            <div className="post--usuario">
                <div className="post--usuario--foto">
                    <img src={props.imgUsuario}/>
                </div>
                <p><b>{props.username}</b></p>
            </div>
            <div className="post--img" >
                <img src={props.img}/>
            </div>
            <div className="post--interacao" >
                <FavoriteBorderIcon className="post--interacao--favorito" />
                <ChatBubbleOutlineIcon className="post--interacao--chat" />
                <SendIcon className="post--interacao--compartilhar" />
            </div>
            <div className="post--conteudo" >
                <p>Curtido por <b>{props.content}</b></p>
            </div>
        </div>
    );
}

export default Post;