import React from 'react';
import './index.css';
import HomeIcon from '@material-ui/icons/Home';
import SearchIcon from '@material-ui/icons/Search';
import PersonalVideoIcon from '@material-ui/icons/PersonalVideo';
import StorefrontIcon from '@material-ui/icons/Storefront';




function Footer(props) {
  return (
    <div className="footer">
        <div className="footer--bts" >
            <HomeIcon className="footer--bts--home" />
            <SearchIcon className="footer--bts--search"/>
            <PersonalVideoIcon className="footer--bts--video"/>
            <StorefrontIcon className="footer--bts--store"/>
            <div className="footer--bts--foto" >
                <img src={props.imgUsuario}/>
            </div>
        </div>
    </div>
  );
}

export default Footer;
