import React,{useState,useEffect} from 'react';
import './App.css';
import Header from './components/Header';
import Post from './components/Post';
import Footer from './components/Footer';

import {db} from './firebase';

function App() {

const [posts,setPosts] = useState([]);

useEffect(()=>{
    db.collection('posts').onSnapshot(snapshot=>{
      setPosts(snapshot.docs.map(function(val){
        return (val.data())
      }));
    });
}, []);

  return (
    <div className="App">
      <Header btn1="Entrar" btn2="Cadastre-se"></Header>
      {
        posts.map((val)=>{
            return(
              <Post  imgUsuario={val.imgUsuario} username={val.username} img={val.img} content={val.content}></Post>
            )
        })
      }
      <Footer imgUsuario="https://upload.wikimedia.org/wikipedia/commons/thumb/7/75/Netflix_icon.svg/1200px-Netflix_icon.svg.png"></Footer>
    </div>
  );
}

export default App;
