import React from 'react';
import './index.css';

function Header(props) {
  return (
    <div className="header">
        <div className="header--logo">
            <a href="#" >
                <img src="https://www.instagram.com/static/images/web/mobile_nav_type_logo.png/735145cfe0a4.png" alt="Logo Instagram"/>
            </a> 
        </div>
        <div className="header--search">
            <input placeholder="Pesquisar" type="text" />
        </div>
        <div className="header--btns">
            <a className="header--btns--signIn" href="#">{props.btn1}</a>
            <a className="header--btns--Register" href="#" >{props.btn2}</a>
        </div>
    </div>
  );
}

export default Header;
